import React from "react";

export default class Offset extends React.Component {
    
    state = {
        height: 0
    }

    constructor() {
        super();
    }

    componentDidMount() {
        var header = document.getElementById("header");
        var cookies = document.getElementById("cookies");

        this.setState({ height: header.offsetHeight });

        window.addEventListener("resize", this.updateOffset.bind(this));
        cookies.addEventListener("transitionstart", this.transitionCookies.bind(this));
    }

    componentWillUnmount() {
        var cookies = document.getElementById("cookies");

        window.removeEventListener("resize", this.updateOffset.bind(this));
        cookies.removeEventListener("transitionstart", this.transitionCookies.bind(this));
    }

    transitionCookies() {
        var header = document.getElementById("header");
        var cookies = document.getElementById("cookies");

        var cookiesOffset = cookies.clientHeight;
        var y = cookies.getBoundingClientRect().y;

        if (y < 5 && y > -10) {
            cookiesOffset = 0;
        } else {
            cookiesOffset = cookies.clientHeight;
        }

        this.setState({ height: header.clientHeight + cookiesOffset });
    }

    updateOffset() {
        var header = document.getElementById("header");
        var cookies = document.getElementById("cookies");

        header.style.top = cookies.getBoundingClientRect().y + cookies.clientHeight + "px";

        if (cookies.getBoundingClientRect().y < -10) {
            cookies.style.top = "-" + cookies.clientHeight + "px";
        }

        this.setState({ height: header.clientHeight + cookies.getBoundingClientRect().y + cookies.clientHeight });
        this.forceUpdate();
    }

    render() {
        const height = parseInt(this.state.height);

        return(
            <div id="offset" style={{ width: "100%", height: height, transition: "height 300ms" }}></div>
        )
    }
}