import React from "react";
import { Link, withRouter } from "react-router-dom";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";

import { isLogged, setStorageItem, getStorageItem } from "../config/config";
import Api from "../config/Api";

import Header from "../components/Header";
import Footer from "../components/Footer";
import Banner from "../components/Banner";

import Loading from "../components/Loading";
import CheckoutForm from "../components/CheckoutForm";

import "../styles/checkout.css";

const promise = loadStripe("pk_test_51Hc5rMFGDIXHKcdbAeI9FeG5b2rAXAu6ATFBsoxB3bBCA7ajJ8UhroPzGVq3eOBDtKqRxVNMr4wKPFnP9zP8Zkts00jnc80SNN");

class Checkout extends React.Component {

    state = {
        offset: 0,

        loading: true,

        name: "",
        email: "",
        phone: "",
        address: "",
        psc: "",
        city: "",
        country: "",

        doterra: true,
        birth: ""
    }

    constructor() {
        super();

        this.autofill = this.autofill.bind(this);
        this.goToPayment = this.goToPayment.bind(this);
    }

    goToPayment() {
        const token = getStorageItem("token");

        const { name, email, phone, address, psc, city, country, doterra, birth } = this.state

        if (!token) {
            const user = {
                name: name,
                email: email,
                phone: phone,
                address: address,
                psc: psc,
                city: city,
                country: country,
            }

            setStorageItem("temp", user);
        }

       if (doterra) {
           setStorageItem("doterra", birth);
       }

        this.props.history.push("/platba");
    }

    async autofill() {
        this.setState({ loading: true })

        const token = getStorageItem("token");

        if (token) {
            const getUser = await Api.getUser(token);
            const user = getUser.user;

            if (user) {
                this.setState({
                    name: user.name,
                    email: user.email,
                    phone: user.phone,
                    address: user.address,
                    psc: user.psc,
                    city: user.city,
                    country: user.country
                });
            }
        }

        this.setState({ loading: false });
    }

    componentDidMount() {
        this.autofill()
    }

    render() {
        return(
            <div className="screen" id="checkout">
                <div className="content">
                    <div className="title">Fakturačné údaje</div>
                    <p className="description">
                        Ak ste členom klubu TerraMia, prihláste sa a nakupujte výhodnejšie. Vaše fakturačné údaje sa vyplnia automaticky.
                    </p>
                    {!getStorageItem("token") ? <div id="checkout-button-login" className="button-filled">Prihlásiť sa</div> : null}

                    {this.state.loading ? <Loading /> : (
                        <div className="panel">
                            <div className="left-panel">
                                <input className="field" type="text" readOnly={getStorageItem("token") ? true : false} required value={this.state.name} placeholder="Meno a priezvisko" onChange={(event) => this.setState({ name: event.target.value})} />
                                <br />
                                <input className="field" type="text" readOnly={getStorageItem("token") ? true : false} required value={this.state.email} placeholder="E-mail" onChange={(event) => this.setState({ email: event.target.value})} />
                                <input className="field" type="text" readOnly={getStorageItem("token") ? true : false} required value={this.state.phone} placeholder="Telefónne číslo" onChange={(event) => this.setState({ phone: event.target.value})} />
                                <br />
                                <input className="field" type="text" readOnly={getStorageItem("token") ? true : false} required value={this.state.address} placeholder="Adresa a číslo domu" onChange={(event) => this.setState({ address: event.target.value})} />
                                <input className="field" type="text" readOnly={getStorageItem("token") ? true : false} required value={this.state.psc} placeholder="PSČ" onChange={(event) => this.setState({ psc: event.target.value})} />
                                <input className="field" type="text" readOnly={getStorageItem("token") ? true : false} required value={this.state.city} placeholder="Mesto" onChange={(event) => this.setState({ city: event.target.value})} />
                                <input className="field" type="text" readOnly={getStorageItem("token") ? true : false} required value={this.state.country} placeholder="Krajina" onChange={(event) => this.setState({ country: event.target.value})} />
                            
                                <div className="button-filled" id="checkout-button-next" onClick={() => this.goToPayment()}>Prejsť na platbu</div>
                            </div>

                            <div className="right-panel">
                                <div className="heading">Zageristrovať sa v doTERRA</div>
                                <div className="text">
                                    Ak ešte nemáte vytvorený účet v doTERRA, my ho s radosťou vytvoríme za Vás a pri tomto nákupe hneď dostanete zľavu 25% na všetky produkty rady doTERRA. Takisto Vám odpustíme registračný poplatok, ktorý by ste zaplatili, keby ste si účet založili sami.
                                </div>

                                <div className="choice">
                                    <div className="item" style={this.state.doterra ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.setState({ doterra: true })}>Chcem účet doTERRA</div>
                                    <div className="item" style={!this.state.doterra ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.setState({ doterra: false })}>Nechcem účet doTERRA</div>
                                </div>

                                {this.state.doterra ? (
                                     <div className="text">
                                         Zadajte svoj dátum narodenia a my Vám vytvoríme Váš doTERRA účet a dáme Vám 25% zľavu na Váš nákup doTERRA produktov.
                                    </div>
                                ) : null}
                                {this.state.doterra ? <input className="field" type="text" required value={this.state.birth} placeholder="Dátum narodenia" onChange={(event) => this.setState({ birth: event.target.value})} /> : null}
                            </div>
                        </div>
                    )}
                </div>
            </div>
        )
    }
}

export default withRouter(Checkout);