import React from "react";

import { enableCookies, disableCookies } from "../config/CookieManager";

import "../styles/cookies.css";

export default function Cookies() {
    return(
        <div id="cookies">
            <div className="text">
                Táto stránka používa cookies na personalizáciu reklamy, marketingové účely a analýzu návštevnosti. Týmto súhlasíte o použití Vášho správania na stránke na marketingové účely.
            </div>

            <div className="button-panel">
                <div className="button" onClick={() => enableCookies()}>Súhlasím</div>
                <div className="button" onClick={() => disableCookies()}>Nesúhlasím</div>
            </div>
        </div>
    )
}