import React from "react";

import { setStorageItem, getStorageItem } from "../config/config";

export function enableCookies() {
    setStorageItem("cookies", true);
    hideCookies();
}

export function disableCookies() {
    setStorageItem("cookies", false);
    hideCookies();
}

export function cookiesEnabled() {
    const cookies = getStorageItem("cookies");

    return (cookies ? true : false);
}

export function showCookies() {
    var header = document.getElementById("header");
    var cookies = document.getElementById("cookies");

    header.style.top = cookies.clientHeight + "px";
    cookies.style.top = "0px";
}

export function hideCookies() {
    var header = document.getElementById("header");
    var cookies = document.getElementById("cookies");

    header.style.top = "0px";
    cookies.style.top = "-" + cookies.clientHeight + "px";
}